<template>
    <section>
        <loader  :is-visible="isLoading"></loader>
        <tg-list 
          :listType="['List']"
          v-show="IsRuleEngineView"
          :IsShowAction="false"
           ModuleName="RuleEngine"
           SubModuleCode="RuleEngine"
           IdentityColumn="RULE_ID"
          :HeaderText="$t('RuleEngine')"
          :ListData="RuleEngineList"
          :HeaderData="Headers"
          :widgets="widgets"
          :HeaderButtons="listheaderbuttons"
          :TotalRecords="TotalRecords"      
          :SearchFields="leftSearchFields" 
          :RenderRowActionMethod="renderActions"
          :ListDataCallBackFunction="FetchData"   
          :SortExp="SortExp"
          :SortBy="SortBy"
          :LegendArray="LegendArray"
          :NorecordfoundText="$t('NoRecordfound')"
          :showCheckBox="true"
          v-on:LeftsearchButtonClick="leftCommonSearch" 
          v-on:PagerButtonClick="pagerMethod"
          v-on:HeaderButtonClick="commonHeaderButtonClick" 
          v-on:ActionButtonClick="actionButtonClick"
          v-on:SortdataButtonClick="sortdata">     
          <template v-slot:slotdata="{ data }"> 
            <template v-if="data.column.COLUMN_NAME == 'RULE_NAME'">
                <a :title="data.row[data.column.COLUMN_NAME]" href="" data-toggle-tooltip="tooltip"> {{data.row[data.column.COLUMN_NAME]}}</a>
            </template> 
            <template v-if="data.column.COLUMN_NAME == 'STATUS_ID'">
                    <listing-status :isfor="'RuleEngine'" v-if="isCRMShowStausDrop(data.row.STATUS_ID, 'Ticketing')"
                        ref="updatestatus" v-bind:dataid="data.row.RULE_ID" data-controller="Crm"
                        v-bind:statuslist="StatusListForListing" v-bind:item="data.STATUS_ID" data-action="index"
                        v-bind:dataselected="data.row.STATUS_ID == 1001 ? 'Active' : 'Inactive'"
                        v-bind:callbackfunction="FetchData" v-bind:isselectedvaluestring="true">
                    </listing-status>
                    <a class="inactive-disabled" v-if="!isCRMShowStausDrop(data.row.status_id, 'Ticketing')">
                        <span v-bind:class="getStatusClass(data.row.STATUS_ID, 'STATUS_ID')">
                            {{ data.row[data.column.COLUMN_NAME] }}
                        </span>
                    </a>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'RULE_NAME'">
                <RouterLink :to="'/RuleEngine/Add/'+data.row.RULE_ID" :title="data.row[data.column.COLUMN_NAME]"  data-toggle-tooltip="tooltip" data-original-title="Click here to edit Rule Engine">
                   {{ data.row[data.column.COLUMN_NAME] }}
                </RouterLink>
            </template>
          </template>  
        </tg-list>
    </section>
</template>
<script>
import DataServices from "@/services/DataService";
import Schema from "@/views/RuleEngine/RuleEngineSchema";
export default {
    data(){
        return{
            isLoading:false,
            StatusListForListing:[],
            searchCondition: '',
            TotalRecords: 0,
            PageNumber: 1,
            PageSize: 10,
            SortBy: '',
            SortExp: '',
            RuleEngineList:[],
            GroupNameList:[],
            IsRuleEngineView:true,
            widgets:[],
            GroupStart: 1,
            GroupEnd: 3,
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            noRecord:false,
            CurrentPage: 1,
            TotalPages: 0,
            LegendArray:[],
            StatusListForListing: [{ STATUS: 'Active', STATUS_ID: 1001, STATUS_COLOR: "#ffffff", STATUS_BG_COLOR: "#28a745" },
            { STATUS: 'Inactive', STATUS_ID: 1002, STATUS_COLOR: "#ffffff", STATUS_BG_COLOR: "#d72435" }],
            moduleGroupId:0,
            Headers: [ 
        {
          COLUMN_NAME: "RULE_NAME",
          DATA_TYPE: "text",
          SORTABLE: true,
          VISIBLE: true,
          DISPLAY_NAME: "Rule Name",
          DISPLAY_ORDER: 1,
          settings:{
            isInSlot:true
          }
        },
        {
          COLUMN_NAME: "MODULE_GROUP_NAME",
          DATA_TYPE: "text",
          SORTABLE: true,
          VISIBLE: true,
          DISPLAY_NAME: 'Module Name',
          DISPLAY_ORDER: 2,
        },
        {
          COLUMN_NAME: "MODIFY_AT",
          DATA_TYPE: "date",
          SORTABLE: true,
          VISIBLE: true,
          DISPLAY_NAME: 'Last Updated Date',
          DISPLAY_ORDER: 3,
        },
        {
          COLUMN_NAME: "UPDATEDBY",
          DATA_TYPE: "text",
          SORTABLE: true,
          VISIBLE: true,
          DISPLAY_NAME: "Last Updated By",
          DISPLAY_ORDER: 4,
        },
        {
                "COLUMN_NAME": "STATUS_ID",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Status",
                "DISPLAY_ORDER": 5
            }
      ],
        }
    },
    created: async function () {
        var vm = this;
        await Schema.bindleftCommonSearchdropdown(this, "FSM", "ModuleList");
        vm.FetchData();
    },
    methods:{
       async FetchData(){
            var vm = this;
            vm.isLoading=true;
            var params = `searchCondition=${vm.searchCondition}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}`
            await DataServices.RuleEngineListing(params).then(function(response){
                vm.Headers.forEach((column) => {
                    column.settings = null;
                    if (column.COLUMN_NAME == 'STATUS_ID') {
                        column.DISPLAY_NAME = 'Status'
                        column.settings = {
                            isInSlot: true,
                            isForTag: true
                        }
                    }
                    if (column.COLUMN_NAME == "RULE_NAME") {
                            column.settings = {
                                isInSlot: true,
                            };
                        }
                });
                var list=response.data.GroupNameList;
                if(list.length > 0){
                    vm.GroupNameList=[];
                    list.forEach(item=>{
                        let obj ={
                            name:vm.$t(item.MODULE_GROUP_NAME),
                            value:item.MODULE_GROUP_ID
                        }
                        if(obj.name!="Field Service Management"){
                            vm.GroupNameList.push(obj);
                        }
                    })
                }
                Schema.leftsearchSchema.forEach(item=>{
                    if(item.fieldName=='Module')
                    {
                        item.listOptions=vm.GroupNameList.sort((a, b) => a.name.toLowerCase() > b.name.toLowerCase() ? 1 : -1);
                    }
                })
                vm.RuleEngineList=JSON.parse(response.data.Listing);
                if(vm.RuleEngineList.length>0){
                    vm.TotalRecords=vm.RuleEngineList[0].TOTAL_RECORDS;
                    vm.listheaderbuttons[1].isdisabled=true;
                    vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                    vm.noRecord=false;
                }else
                {
                    vm.TotalRecords=0;
                    vm.noRecord=true;
                }                                 
                setTimeout(function () {vm.ResponsiveDataTable('tablelistingdata')}, 500);
                vm.isLoading=false;
            })
        },
        renderActions: function (row) { 
        
        },
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchCondition;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        commonHeaderButtonClick: function (event) { 
            switch (event.callbackfunction) {        
                case "commonDeleteData":
                    this.commonDeleteData();
                    break; 
                    case "openaddRuleEngine":
                    this.openAddEditRuleEngine(0);
                    break;
            }
        },
        actionButtonClick: function (callbackfunction, item, index, event) {  
        
        },
        openAddEditRuleEngine(ths) {
            this.$router.push({ name: "AddEditRuleEngine", params: { id: ths } });
        },
        sortdata: async function (sortBy) {
            var vm = this;
            debugger;
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.GroupStart = this.PageNumber;
            this.GroupEnd = this.PageNumber + 2;
            this.FetchData();
        },
        commonDeleteData: function (event) {
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {                       
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("RuleEngine")), function () {   
                    var objectPostString = `ids=${val}&tableName=${'TALYGEN_RE_RULE'}&primaryKey=${'rule_id'}`;       
                    DataServices.DeleteData(objectPostString).then(response => {
                        var box = "";
                        if (response.data == "DeletedSuccess") {
                            vm.listheaderbuttons[1].isdisabled=true;
                            vm.FetchData();
                            box = vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('RuleEngine')]), "Success", true, vm.$t('Alert'));
                        }
                        else {
                            box = vm.ShowAlert(vm.$t('DeletedError', [vm.$t('RuleEngine')]), "failure", true, vm.$t('Alert'));
                        }
                        setTimeout(function () {
                            box.modal('hide');
                        }, 2000);

                    });
                });
            }
            else {
                alert(vm.$t("Selectanyrecordtodelete"));
            }
        },
    }
}
</script>