import DataService from "../../services/DataService";
let buttons =
    [
        {
            title: 'Add Rule',
            iconClass: 'fa fa-plus',        
            callbackfunction: 'openaddRuleEngine',
            additionalClass: '',
            isdisabled: false,
            isvisible: true,
            checkPrivilege: true,
            id:'AddRule',
             
        },
        {
            id: 'DeleteMultiple',
            title: 'Delete',
            iconClass: 'fa-trash-o',
            callbackfunction: 'commonDeleteData',
            isdisabled: true,
            isvisible: true,
            isEnabledOnSelectedRow: true
        }
    ]

    let leftsearchSchema =
    [
        {
            fieldName: 'Search By Rule Name',
            fieldType: "text",
            fieldIcon: "fa fa-star",
            fieldSearchConditionName: ['RULE_NAME'],
            isSearch: false,
            value: "",
            listOptions: [],        
        },
        {
            fieldName: 'Module Name',
            fieldType: "radio",
            fieldIcon: "fa fa-wpforms",
            fieldSearchConditionName: ['ST.MODULE_GROUP_ID'],
            isSearch: false,
            value: "",
            listOptions: [],        
        },
    ];

    async function bindleftCommonSearchdropdown(instance,modulename, filtername) {
        var vm = this;
        await DataService.ModuleGroupNameList().then((response) => {
            vm.leftsearchSchema[1].listOptions=[];
            response.data.forEach(element => {
                let obj = {
                    name: element.MODULE_GROUP_NAME,
                    value: element.MODULE_GROUP_ID
                }
                vm.leftsearchSchema[1].listOptions.push(obj);
            });
        });
    } 

export default { buttons, leftsearchSchema, bindleftCommonSearchdropdown};